.container {
    width: 225px;
    height: 92px;
    background-color: #FAFAFA;
    border-radius: 8px;
    margin-top: 157px;
    margin-left: 105px;
    padding: 8px 8px 30px 8px;
    position: relative;
  }

@media screen and (min-width: 768px) {
    .container  {
        width: 260px;
        height: 100px;
        padding: 12px 12px 34px 12px;
        margin-top: 0;
        margin-left: 75px;
       
     }
  }

  @media screen and (min-width: 1440px) {
    .container  {
        height: 114px;
        padding: 16px 16px 38px 16px;
        margin-top: 0;
        margin-right: 80px;
           
     }
  }  

.text {
  
font-size: 12px;
font-weight: 500;
line-height: 18px;
letter-spacing: -0.24px;
text-align: left;
color: #3E4462;
}

@media screen and (min-width: 768px) {
    .text {
        font-size: 14px;
     }
  }

  @media screen and (min-width: 1440px) {
    .text {
        line-height: 20px;
     }
  }  

.text_active {
 color: #8BAA36;   
}

.btn {
   width: 86px;
   font-size: 10px;
   font-weight: 400;
   line-height: 12px;
   letter-spacing: 0.20px;
   text-align: center;
   color: #3E4462;   
   border: none;
   background-color: transparent;
   margin: 4px 8px 8px 119px;
}

@media screen and (min-width: 768px) {
    .btn {
      
        margin: 4px 12px 12px 142px;
        position: absolute;
        top: 75%;
     }
  }

  @media screen and (min-width: 1440px) {
    .btn {
    top: 70%;
     }
 
  }

 .img {
   width: 18px;
   height: 18px;
   margin-left: 72px;
   margin-top: -11px;
   
   
 } 

 @media screen and (min-width: 768px) {
   .img {
      margin-left: 80px;
      margin-top: -11px;
     
     }
 }
 @media screen and (min-width: 1440px) {
   .img {
      margin-left: 80px;
        
     }
 }
 .crossbolt {
   display: none;
 }

 @media screen and (min-width: 768px) {
  
 .crossbolt {
   width: 186px;
   height: 115px;
   margin-left: 20px;
   display: block;
 }
}